  addJudges = () => {
    if (this.state.count === 0) {
      document.getElementById("Bing2").style.display = "block";
      document.getElementById("Bing3").style.display = "block";
      this.setState({ count: 1 });
    }
    else if (this.state.count === 1) {
      document.getElementById("Bing4").style.display = "block";
      document.getElementById("Bing5").style.display = "block";
      this.setState({ count: 2 });
    }
    else if (this.state.count === 2) {
      document.getElementById("Bing6").style.display = "block";
      document.getElementById("Bing7").style.display = "block";
      this.setState({ count: 3 });
    }
    else {
      document.querySelector(".createDeposit").innerHTML = "Up to seven judges";
    }

  };

  reduceJudges = () => {
    if (this.state.count === 3) {
      document.getElementById("Bing6").style.display = "none";
      document.getElementById("Bing7").style.display = "none";
      this.setState({ count: 2 });
    }
    else if (this.state.count === 2) {
      document.getElementById("Bing4").style.display = "none";
      document.getElementById("Bing5").style.display = "none";
      this.setState({ count: 1 });
    }
    else if (this.state.count === 1) {
      document.getElementById("Bing2").style.display = "none";
      document.getElementById("Bing3").style.display = "none";
      this.setState({ count: 0 });
    }
    else {
      document.querySelector(".createDeposit").innerHTML = "At least one judge";
    }

  };

